.PreMid{

    border:5px double black;
    width: 59%;
    position: relative;
    left:10%;
    top: 10px;
    padding:20px;
    font-size: 14px;
}

.PreMid>h5{

     text-align: center;
     font-size: 14px;
}

.PreMid>.intro>.marks_class_intro{

     text-align: right;
}

.PreMid>.intro>.sub_time_intro{

    display: flex;
    justify-content: space-between;
}

.Section{
    font-size: 16px;
    text-align: center;
    font-weight: bold;
    text-decoration-line: underline;
    margin-top: 10px;
}

.multiple_choice{

    margin-top: 10px;

}


.multi_ques{
    display: flex;
    justify-content: space-between;
}

.multi_ques>div{  
    font-weight: bold;

}


.download_btn{

    position: relative;
    left: 35%;
    top: 6px;
    border-radius: 8px;
    border: none;
    padding: 2px 10px;
    font-size: medium;
    font-weight: bolder;
    color: brown;
    background-color: lightskyblue;
}
